import React, { useState, useEffect, useCallback } from 'react';
import { NavBar, Card, Cell, Tag, Image, Steps, Empty, Button } from 'react-vant';
import { useNavigate, useParams } from 'react-router-dom';
import { exchangeAPI, utils } from '../services/api';
import Icon from '../components/Icon';

const ExchangeDetail = () => {
  const navigate = useNavigate();
  const { id } = useParams();
  const [exchange, setExchange] = useState(null);
  const [loading, setLoading] = useState(true);

  const loadExchangeDetail = useCallback(async () => {
    try {
      setLoading(true);
      const response = await exchangeAPI.getDetail(id);
      setExchange(response.data);
    } catch (error) {
      console.error(':', error);
    } finally {
      setLoading(false);
    }
  }, [id]);

  const getStatusTag = (status) => {
    const statusMap = {
      pending: { color: '#ff976a', text: '' },
      confirmed: { color: '#1989fa', text: '' },
      shipped: { color: '#ff976a', text: '' },
      completed: { color: '#07c160', text: '' },
      cancelled: { color: '#ee0a24', text: '' },
    };
    const config = statusMap[status] || { color: '#969799', text: status };
    return <Tag color={config.color}>{config.text}</Tag>;
  };

  useEffect(() => {
    loadExchangeDetail();
  }, [loadExchangeDetail]);

  const getStatusStep = (status) => {
    const statusSteps = {
      pending: 0,
      confirmed: 1,
      shipped: 2,
      completed: 3,
      cancelled: -1,
    };
    return statusSteps[status] || 0;
  };

  const copyOrderNo = () => {
    if (exchange?.exchange_no) {
      utils.copyToClipboard(exchange.exchange_no);
    }
  };

  if (loading) {
    return (
      <div className="page-container">
        <NavBar 
          title="" 
          leftArrow={<Icon name="arrow-left" />}
          onClickLeft={() => navigate(-1)}
          fixed 
          placeholder
        />
        <div className="loading-container">
          <div>...</div>
        </div>
      </div>
    );
  }

  if (!exchange) {
    return (
      <div className="page-container">
        <NavBar 
          title="" 
          leftArrow={<Icon name="arrow-left" />}
          onClickLeft={() => navigate(-1)}
          fixed 
          placeholder
        />
        <div className="page-content">
          <Card>
            <Empty 
              description="" 
              imageSize={80}
            />
          </Card>
        </div>
      </div>
    );
  }

  return (
    <div className="page-container">
      <NavBar 
        title="" 
        leftArrow={<Icon name="arrow-left" />}
        onClickLeft={() => navigate(-1)}
        fixed 
        placeholder
      />
      
      <div className="page-content">
        {/*  */}
        <Card>
          <div style={{ padding: '16px 0' }}>
            <div style={{ 
              display: 'flex', 
              justifyContent: 'space-between',
              alignItems: 'center',
              marginBottom: '16px'
            }}>
              <div style={{ fontSize: '16px', fontWeight: '600' }}>
                
              </div>
              {getStatusTag(exchange.status)}
            </div>
            
            <Steps 
              active={getStatusStep(exchange.status)}
              direction="vertical"
              activeColor={exchange.status === 'cancelled' ? '#ee0a24' : '#1989fa'}
            >
              <Steps.Item>
                <div style={{ fontSize: '14px', fontWeight: '500' }}></div>
                <div style={{ fontSize: '12px', color: '#969799', marginTop: '2px' }}>
                  {utils.formatDate(exchange.exchange_date, 'YYYY-MM-DD HH:mm')}
                </div>
              </Steps.Item>
              
              {exchange.confirmed_at && (
                <Steps.Item>
                  <div style={{ fontSize: '14px', fontWeight: '500' }}></div>
                  <div style={{ fontSize: '12px', color: '#969799', marginTop: '2px' }}>
                    {utils.formatDate(exchange.confirmed_at, 'YYYY-MM-DD HH:mm')}
                  </div>
                </Steps.Item>
              )}
              
              {exchange.shipped_at && (
                <Steps.Item>
                  <div style={{ fontSize: '14px', fontWeight: '500' }}></div>
                  <div style={{ fontSize: '12px', color: '#969799', marginTop: '2px' }}>
                    {utils.formatDate(exchange.shipped_at, 'YYYY-MM-DD HH:mm')}
                  </div>
                  {exchange.tracking_number && (
                    <div style={{ fontSize: '12px', color: '#1989fa', marginTop: '2px' }}>
                      : {exchange.tracking_number}
                    </div>
                  )}
                </Steps.Item>
              )}
              
              {exchange.completed_at && (
                <Steps.Item>
                  <div style={{ fontSize: '14px', fontWeight: '500' }}></div>
                  <div style={{ fontSize: '12px', color: '#969799', marginTop: '2px' }}>
                    {utils.formatDate(exchange.completed_at, 'YYYY-MM-DD HH:mm')}
                  </div>
                </Steps.Item>
              )}
              
              {exchange.status === 'cancelled' && (
                <Steps.Item>
                  <div style={{ fontSize: '14px', fontWeight: '500', color: '#ee0a24' }}>
                    
                  </div>
                  {exchange.cancelled_at && (
                    <div style={{ fontSize: '12px', color: '#969799', marginTop: '2px' }}>
                      {utils.formatDate(exchange.cancelled_at, 'YYYY-MM-DD HH:mm')}
                    </div>
                  )}
                  {exchange.cancel_reason && (
                    <div style={{ fontSize: '12px', color: '#ee0a24', marginTop: '2px' }}>
                      : {exchange.cancel_reason}
                    </div>
                  )}
                </Steps.Item>
              )}
            </Steps>
          </div>
        </Card>

        {/*  */}
        <Card style={{ marginTop: '12px' }}>
          <div style={{ padding: '16px 0' }}>
            <div style={{ 
              fontSize: '16px', 
              fontWeight: '600', 
              marginBottom: '16px'
            }}>
              
            </div>
            
            <div style={{ display: 'flex', gap: '12px' }}>
              <Image
                src={exchange.product_image}
                alt={exchange.product_name}
                width="80px"
                height="80px"
                fit="cover"
                round
                errorIcon={<Icon name="orders-o" />}
              />
              <div style={{ flex: 1 }}>
                <div style={{ 
                  fontSize: '16px', 
                  fontWeight: '500',
                  marginBottom: '8px',
                  lineHeight: '1.4'
                }}>
                  {exchange.product_name}
                </div>
                <div style={{ 
                  display: 'flex',
                  justifyContent: 'space-between',
                  alignItems: 'center'
                }}>
                  <div style={{ fontSize: '14px', color: '#969799' }}>
                    : {exchange.quantity}
                  </div>
                  <div style={{ 
                    fontSize: '18px', 
                    color: '#ee0a24',
                    fontWeight: '600'
                  }}>
                    {utils.formatNumber(exchange.points_used)}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Card>

        {/*  */}
        <Card style={{ marginTop: '12px' }}>
          <div style={{ padding: '16px 0' }}>
            <div style={{ 
              fontSize: '16px', 
              fontWeight: '600', 
              marginBottom: '16px'
            }}>
              
            </div>
            
            <Cell.Group inset={false}>
              <Cell 
                title="" 
                value={exchange.exchange_no}
                isLink
                onClick={copyOrderNo}
                rightIcon={
                  <Button size="mini" type="primary" plain>
                    
                  </Button>
                }
              />
              <Cell title="ID" value={exchange.user_id} />
              <Cell title="" value={utils.formatDate(exchange.exchange_date, 'YYYY-MM-DD HH:mm:ss')} />
            </Cell.Group>
          </div>
        </Card>

        {/*  */}
        <Card style={{ marginTop: '12px' }}>
          <div style={{ padding: '16px 0' }}>
            <div style={{ 
              fontSize: '16px', 
              fontWeight: '600', 
              marginBottom: '16px',
              display: 'flex',
              alignItems: 'center',
              gap: '8px'
            }}>
              <Icon name="location-o" color="#1989fa" />
              
            </div>
            
            <Cell.Group inset={false}>
              <Cell title="" value={exchange.contact_name || '-'} />
              <Cell
                title=""
                value={exchange.contact_phone || '-'}
                rightIcon={exchange.contact_phone && <Icon name="phone-o" />}
                isLink={!!exchange.contact_phone}
                onClick={() => {
                  if (exchange.contact_phone) {
                    window.location.href = `tel:${exchange.contact_phone}`;
                  }
                }}
              />
              <Cell 
                title="" 
                value={exchange.shipping_address || '-'}
                label={exchange.shipping_address ? '' : undefined}
                isLink={!!exchange.shipping_address}
                onClick={() => {
                  if (exchange.shipping_address) {
                    utils.copyToClipboard(exchange.shipping_address);
                  }
                }}
              />
            </Cell.Group>
          </div>
        </Card>

        {/*  */}
        {exchange.remark && (
          <Card style={{ marginTop: '12px' }}>
            <div style={{ padding: '16px 0' }}>
              <div style={{ 
                fontSize: '16px', 
                fontWeight: '600', 
                marginBottom: '12px'
              }}>
                
              </div>
              <div style={{ 
                fontSize: '14px', 
                color: '#646566',
                lineHeight: '1.6',
                background: '#f7f8fa',
                padding: '12px',
                borderRadius: '6px'
              }}>
                {exchange.remark}
              </div>
            </div>
          </Card>
        )}

        {/*  */}
        <div style={{ height: '20px' }} />
      </div>
    </div>
  );
};

export default ExchangeDetail;


