import React, { useState, useEffect, useCallback } from 'react';
import {
  NavBar,
  Image,
  Button,
  Tag,
  Cell,
  Dialog,
  Field,
  Toast,
  ActionSheet
} from 'react-vant';
import { useNavigate, useParams } from 'react-router-dom';
import { productAPI, exchangeAPI, utils } from '../services/api';
import Icon from '../components/Icon';

const ProductDetail = () => {
  const navigate = useNavigate();
  const { id } = useParams();
  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(true);
  const [exchangeVisible, setExchangeVisible] = useState(false);
  const [exchangeForm, setExchangeForm] = useState({
    userId: '',
    quantity: 1,
    contactName: '',
    contactPhone: '',
    shippingAddress: ''
  });

  useEffect(() => {
    loadProduct();
  }, [loadProduct]);

  const loadProduct = useCallback(async () => {
    try {
      setLoading(true);
      const response = await productAPI.getDetail(id);
      setProduct(response.data);
    } catch (error) {
      console.error(':', error);
      Toast.fail('');
      navigate(-1);
    } finally {
      setLoading(false);
    }
  }, [id]);

  const handleExchange = () => {
    if (!product || product.stock === 0) {
      Toast.fail('');
      return;
    }
    setExchangeVisible(true);
  };

  const handleConfirmExchange = async () => {
    if (!exchangeForm.userId.trim()) {
      Toast.fail('ID');
      return;
    }
    if (!exchangeForm.contactName.trim()) {
      Toast.fail('');
      return;
    }
    if (!exchangeForm.contactPhone.trim()) {
      Toast.fail('');
      return;
    }
    if (!exchangeForm.shippingAddress.trim()) {
      Toast.fail('');
      return;
    }

    try {
      await exchangeAPI.create({
        user_id: exchangeForm.userId.trim(),
        product_id: product.id,
        quantity: exchangeForm.quantity,
        contact_name: exchangeForm.contactName.trim(),
        contact_phone: exchangeForm.contactPhone.trim(),
        shipping_address: exchangeForm.shippingAddress.trim()
      });

      Toast.success('');
      setExchangeVisible(false);
      
      // 
      Dialog.confirm({
        title: '',
        message: '',
      }).then(() => {
        navigate('/exchange-record');
      }).catch(() => {
        navigate(-1);
      });
    } catch (error) {
      console.error(':', error);
    }
  };

  if (loading) {
    return (
      <div className="page-container">
        <NavBar 
          title="" 
          leftArrow={<Icon name="arrow-left" />}
          onClickLeft={() => navigate(-1)}
          fixed 
          placeholder
        />
        <div className="loading-container">
          <div>...</div>
        </div>
      </div>
    );
  }

  if (!product) {
    return (
      <div className="page-container">
        <NavBar 
          title="" 
          leftArrow={<Icon name="arrow-left" />}
          onClickLeft={() => navigate(-1)}
          fixed 
          placeholder
        />
        <div className="empty-state">
          <div className="empty-text"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="page-container">
      <NavBar 
        title="" 
        leftArrow={<Icon name="arrow-left" />}
        onClickLeft={() => navigate(-1)}
        fixed 
        placeholder
      />
      
      <div style={{ paddingBottom: '80px' }}>
        {/*  */}
        <div style={{ position: 'relative' }}>
          <Image
            src={utils.buildImageUrl(product.image_url)}
            alt={product.name}
            width="100%"
            height="300px"
            fit="cover"
            errorIcon="shop-o"
          />
          
          {/*  */}
          <div style={{ 
            position: 'absolute',
            top: '12px',
            left: '12px',
            display: 'flex',
            gap: '6px'
          }}>
            {product.is_hot && (
              <Tag color="#ee0a24">
                <Icon name="fire-o" style={{ marginRight: '4px' }} />
                
              </Tag>
            )}
            {product.is_new && (
              <Tag color="#ff976a">
                <Icon name="new-o" style={{ marginRight: '4px' }} />
                
              </Tag>
            )}
          </div>

          {/*  */}
          {product.stock === 0 && (
            <div style={{
              position: 'absolute',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              background: 'rgba(0, 0, 0, 0.5)',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              color: 'white',
              fontSize: '18px',
              fontWeight: '600'
            }}>
              
            </div>
          )}
        </div>

        {/*  */}
        <div style={{ background: '#fff', padding: '16px' }}>
          <div style={{ fontSize: '18px', fontWeight: '600', marginBottom: '8px' }}>
            {product.name}
          </div>
          
          <div style={{ 
            display: 'flex', 
            alignItems: 'center', 
            justifyContent: 'space-between',
            marginBottom: '12px'
          }}>
            <div>
              <span style={{ 
                fontSize: '24px', 
                fontWeight: '600', 
                color: '#ee0a24' 
              }}>
                {utils.formatNumber(product.points_required)}
              </span>
              {product.original_price && (
                <span style={{ 
                  fontSize: '14px', 
                  color: '#969799',
                  textDecoration: 'line-through',
                  marginLeft: '8px'
                }}>
                  {product.original_price}
                </span>
              )}
            </div>
            
            <div style={{ fontSize: '14px', color: '#969799' }}>
              {product.stock === -1 ? '' : `${product.stock}`}
            </div>
          </div>

          {product.description && (
            <div style={{ 
              fontSize: '14px', 
              color: '#646566', 
              lineHeight: '1.6' 
            }}>
              {product.description}
            </div>
          )}
        </div>

        {/*  */}
        <div style={{ marginTop: '12px', background: '#fff' }}>
          <Cell.Group>
            <Cell title="" value={product.category} />
            <Cell title="" value={`${product.sold_count}`} />
            {product.start_time && product.end_time && (
              <Cell 
                title="" 
                value={`${utils.formatDate(product.start_time)} - ${utils.formatDate(product.end_time)}`}
                label=""
              />
            )}
          </Cell.Group>
        </div>
      </div>

      {/*  */}
      <div className="fixed-bottom-button safe-area">
        <Button 
          type="primary" 
          size="large" 
          block
          disabled={product.stock === 0}
          onClick={handleExchange}
        >
          {product.stock === 0 ? '' : ''}
        </Button>
      </div>

      {/*  */}
      <ActionSheet
        visible={exchangeVisible}
        onClose={() => setExchangeVisible(false)}
        onCancel={() => setExchangeVisible(false)}
        title=""
        closeable
      >
        <div style={{ padding: '16px' }}>
          <Field
            value={exchangeForm.userId}
            onChange={(value) => setExchangeForm(prev => ({ ...prev, userId: value }))}
            label="ID"
            placeholder="ID"
            required
            clearable
          />
          <Field
            value={exchangeForm.contactName}
            onChange={(value) => setExchangeForm(prev => ({ ...prev, contactName: value }))}
            label=""
            placeholder=""
            required
            clearable
          />
          <Field
            value={exchangeForm.contactPhone}
            onChange={(value) => setExchangeForm(prev => ({ ...prev, contactPhone: value }))}
            label=""
            placeholder=""
            required
            clearable
          />
          <Field
            value={exchangeForm.shippingAddress}
            onChange={(value) => setExchangeForm(prev => ({ ...prev, shippingAddress: value }))}
            label=""
            placeholder=""
            type="textarea"
            rows={3}
            required
            clearable
          />
          
          <div style={{ marginTop: '24px', display: 'flex', gap: '12px' }}>
            <Button 
              block 
              onClick={() => setExchangeVisible(false)}
            >
              
            </Button>
            <Button 
              type="primary" 
              block
              onClick={handleConfirmExchange}
            >
              
            </Button>
          </div>
        </div>
      </ActionSheet>
    </div>
  );
};

export default ProductDetail;



