import React from 'react';
import { NavBar, Cell, Grid } from 'react-vant';
import { useNavigate } from 'react-router-dom';
import Icon from '../components/Icon';

const Home = () => {
  const navigate = useNavigate();

  const quickActions = [
    {
      icon: 'scan',
      text: '',
      color: '#1989fa',
      path: '/qr-scanner'
    },
    {
      icon: 'search',
      text: '',
      color: '#07c160',
      path: '/points-query'
    },
    {
      icon: 'shop-o',
      text: '',
      color: '#ff976a',
      path: '/shop'
    },
    {
      icon: 'orders-o',
      text: '',
      color: '#ee0a24',
      path: '/exchange-record'
    }
  ];

  const handleQuickAction = (path) => {
    navigate(path);
  };

  return (
    <div className="page-container">
      <NavBar 
        title="" 
        fixed 
        placeholder
        style={{ background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)' }}
      />
      
      <div className="page-content">
        {/*  */}
        <div className="card" style={{ 
          background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
          color: 'white',
          textAlign: 'center',
          padding: '32px 16px',
          marginBottom: '16px'
        }}>
          <div style={{ fontSize: '24px', fontWeight: '600', marginBottom: '8px' }}>
            
          </div>
          <div style={{ fontSize: '14px', opacity: 0.8 }}>
            
          </div>
        </div>

        {/*  */}
        <div className="card">
          <div className="card-header"></div>
          <Grid columnNum={2} gutter={16} style={{ padding: '16px' }}>
            {quickActions.map((action, index) => (
              <Grid.Item 
                key={index}
                onClick={() => handleQuickAction(action.path)}
                style={{ cursor: 'pointer' }}
              >
                <div style={{ 
                  textAlign: 'center',
                  padding: '20px 0'
                }}>
                  <Icon
                    name={action.icon}
                    size="32px"
                    color={action.color}
                    style={{ marginBottom: '8px' }}
                  />
                  <div style={{ 
                    fontSize: '14px',
                    color: '#323233'
                  }}>
                    {action.text}
                  </div>
                </div>
              </Grid.Item>
            ))}
          </Grid>
        </div>

        {/*  */}
        <div className="card">
          <div className="card-header"></div>
          <Cell.Group inset={false}>
            <Cell
              title=""
              label=""
              icon={<Icon name="scan" />}
              isLink
              onClick={() => navigate('/qr-scanner')}
            />
            <Cell
              title=""
              label="ID"
              icon={<Icon name="search" />}
              isLink
              onClick={() => navigate('/points-query')}
            />
            <Cell
              title=""
              label=""
              icon={<Icon name="shop-o" />}
              isLink
              onClick={() => navigate('/shop')}
            />
            <Cell
              title=""
              label=""
              icon={<Icon name="orders-o" />}
              isLink
              onClick={() => navigate('/exchange-record')}
            />
          </Cell.Group>
        </div>

        {/*  */}
        <div className="card">
          <div className="card-header"></div>
          <div className="card-body">
            <div style={{ lineHeight: '1.6', color: '#646566' }}>
              <p style={{ margin: '0 0 12px 0' }}>
                <strong>1. </strong>
              </p>
              <p style={{ margin: '0 0 12px 0' }}>
                <strong>2. </strong>ID
              </p>
              <p style={{ margin: '0 0 12px 0' }}>
                <strong>3. </strong>
              </p>
              <p style={{ margin: '0 0 0 0' }}>
                <strong>4. </strong>
              </p>
            </div>
          </div>
        </div>

        {/*  */}
        <div className="card">
          <div className="card-header" style={{ 
            display: 'flex', 
            justifyContent: 'space-between',
            alignItems: 'center'
          }}>
            <span></span>
            <span 
              style={{ 
                fontSize: '14px', 
                color: '#1989fa',
                cursor: 'pointer'
              }}
              onClick={() => navigate('/shop')}
            >
               
            </span>
          </div>
          <div style={{ padding: '0 16px 16px' }}>
            <Grid columnNum={2} gutter={12}>
              <Grid.Item>
                <div style={{ 
                  background: '#f7f8fa',
                  borderRadius: '8px',
                  padding: '16px',
                  textAlign: 'center'
                }}>
                  <div style={{ 
                    width: '60px',
                    height: '60px',
                    background: '#1989fa',
                    borderRadius: '50%',
                    margin: '0 auto 12px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: 'white',
                    fontSize: '24px'
                  }}>
                    
                  </div>
                  <div style={{ fontSize: '14px', marginBottom: '4px' }}>
                    
                  </div>
                  <div style={{ fontSize: '12px', color: '#1989fa' }}>
                    200
                  </div>
                </div>
              </Grid.Item>
              <Grid.Item>
                <div style={{ 
                  background: '#f7f8fa',
                  borderRadius: '8px',
                  padding: '16px',
                  textAlign: 'center'
                }}>
                  <div style={{ 
                    width: '60px',
                    height: '60px',
                    background: '#07c160',
                    borderRadius: '50%',
                    margin: '0 auto 12px',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    color: 'white',
                    fontSize: '24px'
                  }}>
                    
                  </div>
                  <div style={{ fontSize: '14px', marginBottom: '4px' }}>
                    
                  </div>
                  <div style={{ fontSize: '12px', color: '#07c160' }}>
                    50
                  </div>
                </div>
              </Grid.Item>
            </Grid>
          </div>
        </div>

        {/*  */}
        <div style={{ height: '20px' }} />
      </div>
    </div>
  );
};

export default Home;
