import React from 'react';
import { NavBar, Cell, Button } from 'react-vant';
import { useNavigate } from 'react-router-dom';
import Icon from '../components/Icon';

const Profile = () => {
  const navigate = useNavigate();

  const menuItems = [
    {
      title: '',
      icon: <Icon name="point-gift-o" color="#1989fa" />,
      path: '/points-query',
      desc: ''
    },
    {
      title: '',
      icon: <Icon name="orders-o" color="#07c160" />,
      path: '/exchange-record',
      desc: ''
    }
  ];

  const helpItems = [
    {
      title: '',
      icon: <Icon name="question-o" color="#ff976a" />,
      path: '/help'
    },
    {
      title: '',
      icon: <Icon name="service-o" color="#ee0a24" />,
      path: '/contact'
    },
    {
      title: '',
      icon: <Icon name="info-o" color="#969799" />,
      path: '/about'
    }
  ];

  return (
    <div className="page-container">
      <NavBar 
        title="" 
        fixed 
        placeholder
      />
      
      <div className="page-content">
        {/*  */}
        <div className="card" style={{ 
          background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
          color: 'white',
          padding: '24px 16px',
          marginBottom: '16px'
        }}>
          <div style={{ 
            display: 'flex', 
            alignItems: 'center',
            marginBottom: '16px'
          }}>
            <div style={{ 
              width: '60px',
              height: '60px',
              background: 'rgba(255, 255, 255, 0.2)',
              borderRadius: '50%',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              marginRight: '16px',
              fontSize: '24px'
            }}>
              <Icon name="user-o" />
            </div>
            <div>
              <div style={{ fontSize: '18px', fontWeight: '600', marginBottom: '4px' }}>
                
              </div>
              <div style={{ fontSize: '14px', opacity: 0.8 }}>
                
              </div>
            </div>
          </div>
          
          <div style={{ 
            background: 'rgba(255, 255, 255, 0.1)',
            borderRadius: '8px',
            padding: '12px',
            textAlign: 'center'
          }}>
            <div style={{ fontSize: '14px', opacity: 0.8, marginBottom: '4px' }}>
              ID
            </div>
            <Button 
              size="small" 
              round
              style={{ 
                background: 'rgba(255, 255, 255, 0.2)',
                border: '1px solid rgba(255, 255, 255, 0.3)',
                color: 'white'
              }}
              onClick={() => navigate('/points-query')}
            >
              
            </Button>
          </div>
        </div>

        {/*  */}
        <div className="card">
          <div className="card-header"></div>
          <Cell.Group inset={false}>
            {menuItems.map((item, index) => (
              <Cell
                key={index}
                title={item.title}
                label={item.desc}
                icon={item.icon}
                isLink
                onClick={() => navigate(item.path)}
              />
            ))}
          </Cell.Group>
        </div>

        {/*  */}
        <div className="card">
          <div className="card-header"></div>
          <Cell.Group inset={false}>
            {helpItems.map((item, index) => (
              <Cell
                key={index}
                title={item.title}
                icon={item.icon}
                isLink
                onClick={() => {
                  if (item.path === '/contact') {
                    // 
                    // 
                    window.location.href = 'tel:400-123-4567';
                  } else if (item.path === '/help') {
                    // 
                    navigate('/help');
                  } else if (item.path === '/about') {
                    // 
                    navigate('/about');
                  }
                }}
              />
            ))}
          </Cell.Group>
        </div>

        {/*  */}
        <div className="card">
          <div className="card-header"></div>
          <Cell.Group inset={false}>
            <Cell title="" value="v1.0.0" />
            <Cell title="" value="2024-01-01" />
          </Cell.Group>
        </div>

        {/*  */}
        <div style={{ height: '20px' }} />
      </div>
    </div>
  );
};

export default Profile;
