import React, { useState, useEffect, useCallback } from 'react';
import {
  NavBar,
  Field,
  Button,
  Card,
  Cell,
  Empty,
  Toast
} from 'react-vant';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { userAPI, pointsAPI, utils } from '../services/api';
import Icon from '../components/Icon';

const PointsQuery = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [userId, setUserId] = useState('');
  const [loading, setLoading] = useState(false);
  const [userInfo, setUserInfo] = useState(null);
  const [pointsRecords, setPointsRecords] = useState([]);
  const [hasQueried, setHasQueried] = useState(false);

  const handleQuery = useCallback(async (rawUserId) => {
    const targetUserId = (rawUserId || '').trim();
    
    if (!targetUserId) {
      Toast.fail('ID');
      return;
    }
    
    try {
      setLoading(true);
      
      // 
      const userResponse = await userAPI.getPoints(targetUserId);
      const userData = userResponse?.data?.user || userResponse?.data;
      if (!userData) {
        throw new Error('');
      }
      setUserInfo(userData);
      
      // 
      const recordsResponse = await pointsAPI.getRecords(targetUserId, {
        page: 1,
        pageSize: 20
      });
      setPointsRecords(recordsResponse.data.list || []);
      
      setHasQueried(true);
    } catch (error) {
      console.error(':', error);
      Toast.fail(error?.response?.data?.message || error.message || '');
      setUserInfo(null);
      setPointsRecords([]);
      setHasQueried(true);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    // URLID
    const userIdFromUrl = searchParams.get('userId');
    if (userIdFromUrl) {
      setUserId(userIdFromUrl);
      // 
      handleQuery(userIdFromUrl);
    }
  }, [searchParams, handleQuery]);


  const handleReset = () => {
    setUserId('');
    setUserInfo(null);
    setPointsRecords([]);
    setHasQueried(false);
  };

  const getSourceText = (source) => {
    const sourceMap = {
      import: '',
      exchange: '',
      manual: '',
      expire: ''
    };
    return sourceMap[source] || source;
  };

  return (
    <div className="page-container">
      <NavBar 
        title="" 
        leftArrow={<Icon name="arrow-left" />}
        onClickLeft={() => navigate(-1)}
        fixed 
        placeholder
      />
      
      <div className="page-content">
        {/*  */}
        <Card>
          <div style={{ padding: '16px 0' }}>
            <Field
              value={userId}
              onChange={setUserId}
              label="ID"
              placeholder="ID"
              clearable
              maxlength={50}
            />
            <div style={{ marginTop: '16px', display: 'flex', gap: '12px' }}>
              <Button
                type="primary"
                block
                loading={loading}
                onClick={() => handleQuery(userId)}
              >
                
              </Button>
              {hasQueried && (
                <Button 
                  block 
                  onClick={handleReset}
                  style={{ flex: '0 0 80px' }}
                >
                  
                </Button>
              )}
            </div>
          </div>
        </Card>

        {/*  */}
        {hasQueried && (
          <>
            {userInfo ? (
              <>
                {/*  */}
                <Card style={{ marginTop: '12px' }}>
                  <div className="stats-card">
                    <div className="stats-number">
                      {utils.formatNumber(userInfo.available_points)}
                    </div>
                    <div className="stats-label"></div>
                  </div>
                </Card>

                {/*  */}
                <Card style={{ marginTop: '12px' }}>
                  <div style={{ padding: '16px 0' }}>
                    <div style={{ 
                      fontSize: '16px', 
                      fontWeight: '600', 
                      marginBottom: '16px',
                      display: 'flex',
                      alignItems: 'center',
                      gap: '8px'
                    }}>
                      <Icon name="point-gift-o" color="#1989fa" />
                      
                    </div>
                    
                    <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '16px' }}>
                      <div style={{ textAlign: 'center' }}>
                        <div style={{ fontSize: '20px', fontWeight: '600', color: '#1989fa' }}>
                          {utils.formatNumber(userInfo.total_points)}
                        </div>
                        <div style={{ fontSize: '14px', color: '#969799', marginTop: '4px' }}>
                          
                        </div>
                      </div>
                      <div style={{ textAlign: 'center' }}>
                        <div style={{ fontSize: '20px', fontWeight: '600', color: '#ee0a24' }}>
                          {utils.formatNumber(userInfo.used_points)}
                        </div>
                        <div style={{ fontSize: '14px', color: '#969799', marginTop: '4px' }}>
                          
                        </div>
                      </div>
                    </div>

                    <div style={{ height: '1px', background: '#ebedf0', margin: '16px 0' }} />

                    <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '16px' }}>
                      <div style={{ textAlign: 'center' }}>
                        <div style={{ fontSize: '16px', fontWeight: '600', color: '#969799' }}>
                          {utils.formatNumber(userInfo.expired_points)}
                        </div>
                        <div style={{ fontSize: '14px', color: '#969799', marginTop: '4px' }}>
                          
                        </div>
                      </div>
                      <div style={{ textAlign: 'center' }}>
                        <div style={{ fontSize: '16px', fontWeight: '600', color: '#07c160' }}>
                          {userInfo.is_new_user ? '' : ''}
                        </div>
                        <div style={{ fontSize: '14px', color: '#969799', marginTop: '4px' }}>
                          
                        </div>
                      </div>
                    </div>
                  </div>
                </Card>

                {/*  */}
                <Card style={{ marginTop: '12px' }}>
                  <div style={{ padding: '16px 0' }}>
                    <div style={{ 
                      fontSize: '16px', 
                      fontWeight: '600', 
                      marginBottom: '16px',
                      display: 'flex',
                      alignItems: 'center',
                      gap: '8px'
                    }}>
                      <Icon name="clock-o" color="#1989fa" />
                      
                    </div>
                    
                    {pointsRecords.length > 0 ? (
                      <Cell.Group inset={false}>
                        {pointsRecords.map((record, index) => (
                          <Cell
                            key={record.id || index}
                            title={
                              <div>
                                <div style={{ marginBottom: '4px' }}>
                                  {getSourceText(record.source)}
                                </div>
                                <div style={{ fontSize: '12px', color: '#969799' }}>
                                  {utils.formatDate(record.created_at, 'YYYY-MM-DD HH:mm')}
                                </div>
                              </div>
                            }
                            label={record.description}
                            value={
                              <div style={{ textAlign: 'right' }}>
                                <div style={{ 
                                  fontSize: '16px', 
                                  fontWeight: '600',
                                  color: record.points > 0 ? '#07c160' : '#ee0a24'
                                }}>
                                  {record.points > 0 ? '+' : ''}{utils.formatNumber(record.points)}
                                </div>
                                <div style={{ fontSize: '12px', color: '#969799', marginTop: '2px' }}>
                                  : {utils.formatNumber(record.balance_after)}
                                </div>
                              </div>
                            }
                            border={index < pointsRecords.length - 1}
                          />
                        ))}
                      </Cell.Group>
                    ) : (
                      <Empty 
                        description="" 
                        imageSize={60}
                      />
                    )}
                  </div>
                </Card>
              </>
            ) : (
              <Card style={{ marginTop: '12px' }}>
                <Empty 
                  description="" 
                  imageSize={80}
                >
                  <div style={{ marginTop: '16px', color: '#969799', fontSize: '14px' }}>
                    ID
                  </div>
                </Empty>
              </Card>
            )}
          </>
        )}

        {/*  */}
        {!hasQueried && (
          <Card style={{ marginTop: '12px' }}>
            <div style={{ padding: '16px 0' }}>
              <div style={{ 
                fontSize: '16px', 
                fontWeight: '600', 
                marginBottom: '12px',
                color: '#323233'
              }}>
                
              </div>
              <div style={{ lineHeight: '1.6', color: '#646566', fontSize: '14px' }}>
                <p style={{ margin: '0 0 8px 0' }}>
                   ID
                </p>
                <p style={{ margin: '0 0 8px 0' }}>
                   ID
                </p>
                <p style={{ margin: '0 0 8px 0' }}>
                   ID
                </p>
                <p style={{ margin: '0' }}>
                   
                </p>
              </div>
            </div>
          </Card>
        )}

        {/*  */}
        <div style={{ height: '20px' }} />
      </div>
    </div>
  );
};

export default PointsQuery;


