import React, { useState, useEffect, useRef } from 'react';
import {
  NavBar,
  Card,
  Button,
  Toast,
  Dialog
} from 'react-vant';
import { useNavigate } from 'react-router-dom';
import Icon from '../components/Icon';

const QRScanner = () => {
  const navigate = useNavigate();
  const videoRef = useRef(null);
  const canvasRef = useRef(null);
  const [isScanning, setIsScanning] = useState(false);
  const [stream, setStream] = useState(null);
  const [error, setError] = useState('');

  useEffect(() => {
    return () => {
      // 
      if (stream) {
        stream.getTracks().forEach(track => track.stop());
      }
    };
  }, [stream]);

  const startCamera = async () => {
    try {
      setError('');
      const mediaStream = await navigator.mediaDevices.getUserMedia({
        video: { 
          facingMode: 'environment', // 
          width: { ideal: 1280 },
          height: { ideal: 720 }
        }
      });
      
      setStream(mediaStream);
      if (videoRef.current) {
        videoRef.current.srcObject = mediaStream;
        videoRef.current.play();
      }
      setIsScanning(true);
      
      // 
      startScanning();
    } catch (err) {
      console.error(':', err);
      setError('');
      Toast.fail('');
    }
  };

  const stopCamera = () => {
    if (stream) {
      stream.getTracks().forEach(track => track.stop());
      setStream(null);
    }
    setIsScanning(false);
  };

  const startScanning = () => {
    const scanInterval = setInterval(() => {
      if (videoRef.current && canvasRef.current && isScanning) {
        const video = videoRef.current;
        const canvas = canvasRef.current;
        const context = canvas.getContext('2d');
        
        canvas.width = video.videoWidth;
        canvas.height = video.videoHeight;
        context.drawImage(video, 0, 0, canvas.width, canvas.height);
        
        const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
        
        //  jsQR
        // 
        //  jsQR 
        
        // 
        if (Math.random() < 0.1) { // 10% 
          const mockQRData = 'USER_ID_12345'; // ID
          handleScanResult(mockQRData);
          clearInterval(scanInterval);
        }
      }
    }, 500);

    // 10
    setTimeout(() => {
      clearInterval(scanInterval);
    }, 10000);
  };

  const handleScanResult = (data) => {
    stopCamera();
    
    // 
    if (data && data.startsWith('USER_ID_')) {
      const userId = data.replace('USER_ID_', '');
      Dialog.confirm({
        title: '',
        message: `ID: ${userId}`,
      }).then(() => {
        // ID
        navigate(`/points-query?userId=${userId}`);
      }).catch(() => {
        // 
        startCamera();
      });
    } else {
      Toast.fail('');
      setTimeout(() => {
        startCamera();
      }, 1000);
    }
  };

  const handleManualInput = () => {
    navigate('/points-query');
  };

  return (
    <div className="page-container">
      <NavBar 
        title="" 
        leftArrow={<Icon name="arrow-left" />}
        onClickLeft={() => navigate(-1)}
        fixed 
        placeholder
      />
      
      <div className="page-content">
        {/*  */}
        <Card>
          <div style={{ 
            position: 'relative',
            width: '100%',
            height: '300px',
            background: '#000',
            borderRadius: '8px',
            overflow: 'hidden',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center'
          }}>
            {isScanning ? (
              <>
                <video
                  ref={videoRef}
                  style={{
                    width: '100%',
                    height: '100%',
                    objectFit: 'cover'
                  }}
                  playsInline
                  muted
                />
                <canvas
                  ref={canvasRef}
                  style={{ display: 'none' }}
                />
                
                {/*  */}
                <div style={{
                  position: 'absolute',
                  top: '50%',
                  left: '50%',
                  transform: 'translate(-50%, -50%)',
                  width: '200px',
                  height: '200px',
                  border: '2px solid #1989fa',
                  borderRadius: '8px',
                  background: 'transparent'
                }}>
                  {/*  */}
                  <div style={{
                    position: 'absolute',
                    top: '-2px',
                    left: '-2px',
                    width: '20px',
                    height: '20px',
                    borderTop: '4px solid #1989fa',
                    borderLeft: '4px solid #1989fa'
                  }} />
                  <div style={{
                    position: 'absolute',
                    top: '-2px',
                    right: '-2px',
                    width: '20px',
                    height: '20px',
                    borderTop: '4px solid #1989fa',
                    borderRight: '4px solid #1989fa'
                  }} />
                  <div style={{
                    position: 'absolute',
                    bottom: '-2px',
                    left: '-2px',
                    width: '20px',
                    height: '20px',
                    borderBottom: '4px solid #1989fa',
                    borderLeft: '4px solid #1989fa'
                  }} />
                  <div style={{
                    position: 'absolute',
                    bottom: '-2px',
                    right: '-2px',
                    width: '20px',
                    height: '20px',
                    borderBottom: '4px solid #1989fa',
                    borderRight: '4px solid #1989fa'
                  }} />
                </div>
                
                {/*  */}
                <div style={{
                  position: 'absolute',
                  top: '50%',
                  left: '50%',
                  transform: 'translate(-50%, -50%)',
                  width: '180px',
                  height: '2px',
                  background: 'linear-gradient(90deg, transparent, #1989fa, transparent)',
                  animation: 'scan 2s linear infinite'
                }} />
              </>
            ) : (
              <div style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                color: '#969799'
              }}>
                <Icon name="scan" size="48px" style={{ marginBottom: '12px' }} />
                <div style={{ fontSize: '14px' }}>
                  {error || ''}
                </div>
              </div>
            )}
          </div>
        </Card>

        {/*  */}
        <Card style={{ marginTop: '16px' }}>
          <div style={{ padding: '16px 0' }}>
            <div style={{ 
              display: 'flex', 
              gap: '12px',
              flexDirection: 'column'
            }}>
              {!isScanning ? (
                <Button 
                  type="primary" 
                  size="large"
                  onClick={startCamera}
                  icon={<Icon name="scan" />}
                >
                  
                </Button>
              ) : (
                <Button 
                  type="default" 
                  size="large"
                  onClick={stopCamera}
                >
                  
                </Button>
              )}
              
              <Button 
                type="default" 
                size="large"
                onClick={handleManualInput}
              >
                ID
              </Button>
            </div>
          </div>
        </Card>

        {/*  */}
        <Card style={{ marginTop: '16px' }}>
          <div style={{ padding: '16px 0' }}>
            <div style={{ 
              fontSize: '16px', 
              fontWeight: '600', 
              marginBottom: '12px'
            }}>
              
            </div>
            <div style={{ 
              fontSize: '14px', 
              color: '#646566',
              lineHeight: '1.6'
            }}>
              <div style={{ marginBottom: '8px' }}>
                1. ""
              </div>
              <div style={{ marginBottom: '8px' }}>
                2. 
              </div>
              <div style={{ marginBottom: '8px' }}>
                3. 
              </div>
              <div>
                4. ID
              </div>
            </div>
          </div>
        </Card>

        {/*  */}
        <div style={{ height: '20px' }} />
      </div>

      <style jsx>{`
        @keyframes scan {
          0% {
            transform: translate(-50%, -90px);
          }
          100% {
            transform: translate(-50%, 90px);
          }
        }
      `}</style>
    </div>
  );
};

export default QRScanner;
