import React, { useState } from 'react';
import { NavBar, Field, Button, Card, List, Tag, Empty, Image, PullRefresh, Toast } from 'react-vant';
import { useNavigate } from 'react-router-dom';
import { exchangeAPI, utils } from '../services/api';
import Icon from '../components/Icon';

const PAGE_SIZE = 10;

const statusMeta = {
  pending: { color: '#ff976a', text: '' },
  confirmed: { color: '#1989fa', text: '' },
  shipped: { color: '#ff976a', text: '' },
  completed: { color: '#07c160', text: '' },
  cancelled: { color: '#ee0a24', text: '' },
};

const ExchangeRecord = () => {
  const navigate = useNavigate();
  const [userId, setUserId] = useState('');
  const [loading, setLoading] = useState(false);
  const [exchanges, setExchanges] = useState([]);
  const [hasQueried, setHasQueried] = useState(false);
  const [finished, setFinished] = useState(false);
  const [page, setPage] = useState(1);

  const fetchRecords = async (reset = false) => {
    const trimmedId = userId.trim();
    if (!trimmedId) {
      Toast.fail('ID');
      return;
    }

    if (loading) return;

    try {
      setLoading(true);
      const currentPage = reset ? 1 : page;

      const response = await exchangeAPI.getMyExchanges(trimmedId, {
        page: currentPage,
        pageSize: PAGE_SIZE
      });

      const list = response.data.list || [];

      if (reset) {
        setExchanges(list);
        setPage(2);
      } else {
        setExchanges(prev => [...prev, ...list]);
        setPage(prev => prev + 1);
      }

      setFinished(list.length < PAGE_SIZE);
      setHasQueried(true);
    } catch (error) {
      console.error(':', error);
      if (reset) {
        setExchanges([]);
      }
      setHasQueried(true);
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async () => {
    setFinished(false);
    setPage(1);
    await fetchRecords(true);
  };

  const handleRefresh = async () => {
    setFinished(false);
    setPage(1);
    await fetchRecords(true);
  };

  const handleLoadMore = async () => {
    if (loading || finished) return;
    await fetchRecords(false);
  };

  const handleReset = () => {
    setUserId('');
    setExchanges([]);
    setHasQueried(false);
    setFinished(false);
    setPage(1);
  };

  const renderStatusTag = (status) => {
    const meta = statusMeta[status] || { color: '#969799', text: status };
    return <Tag color={meta.color}>{meta.text}</Tag>;
  };

  const handleExchangeClick = (exchange) => {
    navigate(`/exchange/${exchange.id}`);
  };

  return (
    <div className="page-container">
      <NavBar
        title=""
        leftArrow={<Icon name="arrow-left" />}
        onClickLeft={() => navigate(-1)}
        fixed
        placeholder
      />

      <div className="page-content">
        {/*  */}
        <Card>
          <div style={{ padding: '16px 0' }}>
            <Field
              value={userId}
              onChange={setUserId}
              label="ID"
              placeholder="ID"
              clearable
              maxlength={50}
            />
            <div style={{ marginTop: '16px', display: 'flex', gap: '12px' }}>
              <Button
                type="primary"
                block
                loading={loading}
                onClick={handleSubmit}
                icon={<Icon name="search" />}
              >
                
              </Button>
              {hasQueried && (
                <Button block onClick={handleReset} style={{ flex: '0 0 80px' }}>
                  
                </Button>
              )}
            </div>
          </div>
        </Card>

        {/*  */}
        {hasQueried && (
          <div style={{ marginTop: '12px' }}>
            {exchanges.length > 0 ? (
              <PullRefresh onRefresh={handleRefresh}>
                <List
                  loading={loading}
                  finished={finished}
                  onLoad={handleLoadMore}
                  finishedText=""
                  loadingText="..."
                >
                  {exchanges.map(exchange => (
                    <Card
                      key={exchange.id}
                      style={{ marginBottom: '12px', cursor: 'pointer' }}
                      onClick={() => handleExchangeClick(exchange)}
                    >
                      <div style={{ padding: '12px 0' }}>
                        {/*  */}
                        <div style={{
                          display: 'flex',
                          justifyContent: 'space-between',
                          alignItems: 'center',
                          marginBottom: '12px'
                        }}>
                          <div style={{ fontSize: '12px', color: '#969799' }}>
                            : {exchange.exchange_no}
                          </div>
                          {renderStatusTag(exchange.status)}
                        </div>

                        {/*  */}
                        <div style={{ display: 'flex', gap: '12px', marginBottom: '12px' }}>
                          <Image
                            src={exchange.product_image}
                            alt={exchange.product_name}
                            width="60px"
                            height="60px"
                            fit="cover"
                            lazyload
                            errorIcon={<Icon name="photo-fail" />}
                            style={{ borderRadius: '8px' }}
                          />
                          <div style={{ flex: 1 }}>
                            <div style={{ fontSize: '16px', fontWeight: '600', marginBottom: '6px' }}>
                              {exchange.product_name}
                            </div>
                            <div style={{ fontSize: '14px', color: '#969799', marginBottom: '6px' }}>
                              : {exchange.quantity} | : {utils.formatNumber(exchange.points_used)}
                            </div>
                            <div style={{ fontSize: '12px', color: '#969799' }}>
                              : {exchange.contact_name || '-'} | : {exchange.contact_phone || '-'}
                            </div>
                          </div>
                        </div>

                        {/*  */}
                        <div style={{
                          fontSize: '12px',
                          color: '#969799',
                          display: 'flex',
                          justifyContent: 'space-between'
                        }}>
                          <span>: {utils.formatDate(exchange.exchange_date, 'MM-DD HH:mm')}</span>
                          <span> &gt;</span>
                        </div>
                      </div>
                    </Card>
                  ))}
                </List>
              </PullRefresh>
            ) : (
              <Card>
                <Empty
                  description=""
                  imageSize={80}
                >
                  <div style={{ marginTop: '16px', color: '#969799', fontSize: '14px' }}>
                    ID
                  </div>
                </Empty>
              </Card>
            )}
          </div>
        )}

        {/*  */}
        {!hasQueried && (
          <Card style={{ marginTop: '12px' }}>
            <div style={{ padding: '16px 0' }}>
              <div style={{
                fontSize: '16px',
                fontWeight: '600',
                marginBottom: '12px',
                color: '#323233'
              }}>
                
              </div>
              <div style={{ lineHeight: '1.6', color: '#646566', fontSize: '14px' }}>
                <p style={{ margin: '0 0 8px 0' }}>
                   ID
                </p>
                <p style={{ margin: '0 0 8px 0' }}>
                   
                </p>
                <p style={{ margin: '0 0 8px 0' }}>
                   
                </p>
                <p style={{ margin: 0 }}>
                   
                </p>
              </div>
            </div>
          </Card>
        )}

        {/*  */}
        <div style={{ height: '20px' }} />
      </div>
    </div>
  );
};

export default ExchangeRecord;
