import axios from 'axios';
import { Toast } from 'react-vant';

// axios
const api = axios.create({
  baseURL: '/api',
  timeout: 30000,
  headers: {
    'Content-Type': 'application/json',
  },
});

// 
api.interceptors.request.use(
  (config) => {
    // 
    if (config.showLoading !== false) {
      Toast.loading({
        message: '...',
        forbidClick: true,
        duration: 0,
      });
    }
    return config;
  },
  (error) => {
    Toast.clear();
    return Promise.reject(error);
  }
);

// 
api.interceptors.response.use(
  (response) => {
    Toast.clear();
    const { data } = response;
    if (data.success === false) {
      Toast.fail(data.message || '');
      return Promise.reject(new Error(data.message || ''));
    }
    return data;
  },
  (error) => {
    Toast.clear();
    const { response } = error;
    if (response) {
      const { status, data } = response;
      switch (status) {
        case 404:
          Toast.fail('');
          break;
        case 500:
          Toast.fail('');
          break;
        default:
          Toast.fail(data?.message || '');
      }
    } else {
      Toast.fail('');
    }
    return Promise.reject(error);
  }
);

// API
export const userAPI = {
  // 
  getPoints: (userId) => api.get(`/users/${userId}/points`, { showLoading: false }),
  
  // 
  getInfo: (userId) => api.get(`/users/${userId}`, { showLoading: false }),
};

// API
export const productAPI = {
  // 
  getList: (params) => api.get('/products', { 
    params: { ...params, is_active: true },
    showLoading: false 
  }),
  
  // 
  getDetail: (id) => api.get(`/products/${id}`, { showLoading: false }),
};

// API
export const exchangeAPI = {
  // 
  create: (data) => api.post('/exchanges', data),
  
  // 
  getMyExchanges: (userId, params) => api.get('/exchanges', { 
    params: { ...params, user_id: userId },
    showLoading: false 
  }),
  
  // 
  getDetail: (id) => api.get(`/exchanges/${id}`, { showLoading: false }),
};

// API
export const pointsAPI = {
  // 
  getRecords: (userId, params) => api.get('/points/records', { 
    params: { ...params, user_id: userId },
    showLoading: false 
  }),
};

// 
export const utils = {
  // URL
  buildImageUrl: (path) => {
    if (!path) return '';
    if (/^https?:\/\//i.test(path)) return path;
    const baseUrl = process.env.REACT_APP_API_URL || window.location.origin;
    return `${baseUrl}${path.startsWith('/') ? path : `/${path}`}`;
  },
  
  // 
  formatNumber: (num) => {
    if (num === null || num === undefined) return '0';
    return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  },
  
  // 
  formatDate: (date, format = 'YYYY-MM-DD') => {
    if (!date) return '-';
    const d = new Date(date);
    const year = d.getFullYear();
    const month = String(d.getMonth() + 1).padStart(2, '0');
    const day = String(d.getDate()).padStart(2, '0');
    const hour = String(d.getHours()).padStart(2, '0');
    const minute = String(d.getMinutes()).padStart(2, '0');
    
    return format
      .replace('YYYY', year)
      .replace('MM', month)
      .replace('DD', day)
      .replace('HH', hour)
      .replace('mm', minute);
  },
  
  // 
  getStatusText: (status) => {
    const statusMap = {
      pending: '',
      confirmed: '',
      shipped: '',
      completed: '',
      cancelled: '',
    };
    return statusMap[status] || status;
  },
  
  // 
  getStatusColor: (status) => {
    const colorMap = {
      pending: '#ff976a',
      confirmed: '#1989fa',
      shipped: '#ff976a',
      completed: '#07c160',
      cancelled: '#ee0a24',
    };
    return colorMap[status] || '#969799';
  },
  
  // 
  isWeChat: () => {
    return /micromessenger/i.test(navigator.userAgent);
  },
  
  // 
  isMobile: () => {
    return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
  },
  
  // 
  copyToClipboard: (text) => {
    if (navigator.clipboard) {
      return navigator.clipboard.writeText(text).then(() => {
        Toast.success('');
      }).catch(() => {
        Toast.fail('');
      });
    } else {
      // 
      const textArea = document.createElement('textarea');
      textArea.value = text;
      document.body.appendChild(textArea);
      textArea.select();
      try {
        document.execCommand('copy');
        Toast.success('');
      } catch (err) {
        Toast.fail('');
      }
      document.body.removeChild(textArea);
    }
  },
  
  // 
  share: (data) => {
    if (navigator.share) {
      return navigator.share(data);
    } else {
      // 
      if (data.url) {
        return utils.copyToClipboard(data.url);
      }
      Toast.fail('');
    }
  },
};

export default api;
